<template>
  <div class="home-page">
    <div class="container page">
      <br><h2><b>Food and Beverage</b></h2><br>
      <div class="row justify-content-center">
        <div v-for="(merchant, idx) in merchants" :key="idx"
            class="col-xs-6 col-md-3 itemGrid">
          <div
            class="merchantCard"
            @click="openMerchant(idx)"
          >
            <div class="merchantImage img-fluid">
              <div v-if="!merchant.currentlyOp" class="notOpOverlay">
                <div class="text">Closed</div>
              </div>
              <img class="img-fluid" :src="merchant.image" />
            </div>
            <small>
              <span v-for="(tag, idx2) in merchant.tags" :key="idx2" class="merchantTags">
                {{ tag }}
                <span v-if="idx2 != merchant.tags.length - 1"> - </span>
              </span>
              </small><br>
            <b>{{ merchant.name.toUpperCase() }}</b><br>
            <span>{{ merchant.address }}</span><br>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>


<script>
import { mapGetters } from "vuex";
import { FETCH_MERCHANTS } from "@/store/actions.type";

import { NEW_CART } from "@/store/mutations.type";

export default {
  name: "viewHome",
  data() {
    return { }
  },
  computed: {
    // ...mapState({
    //   errors: state => state.auth.errors
    // }),
    ...mapGetters(["currentCart", "merchants"])
  },
  mounted() {
    this.fetchMerchants();
  },
  methods: {
    fetchMerchants() {
      this.$store.dispatch(FETCH_MERCHANTS);
    },

    openMerchant (merchantIdx) {
      // console.log("currentCart", this.currentCart.merchant.friendlyId)
      let merchant = this.merchants[merchantIdx]
      // console.log("nextMerchant", merchant.friendlyId)
      if(this.currentCart.items.length >= 1){
        if (this.currentCart.merchant.friendlyId != merchant.friendlyId){
          if (window.confirm(this.currentCart.merchant.name + ' cart is found, would you like to proceed to visit ' + merchant.name + '?')) {
            this.$store.commit(NEW_CART, merchant, { root: true });
            this.$router.push({ name: "merchant", params: { 'merchantId': merchant.friendlyId } })
          }else{
            return 0;
          }
        }
      }else{
        this.$store.commit(NEW_CART, merchant, { root: true });
      }
      this.$router.push({ name: "merchant", params: { 'merchantId': merchant.friendlyId } })
    },
  }
};
</script>


<style scoped>
  .itemGrid{
    margin-bottom: 20px;
  }
  
  .merchantTags{
    color: #ffba00;
    padding-bottom: -50px;
  }

  .merchantCard {
    color: black;
  }

  .merchantImage{
    position: relative;
  }

  .notOpOverlay {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100%;
    width: 100%;
    opacity: 0.7;
    background-color: #777;
  }

  .notOpOverlay > .text {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    text-align: center;
    font-weight: bold;
    color: black;
  }

</style>


