import { MerchantsService } from "@/common/merchants.service";

import { FETCH_MERCHANT } from "@/store/actions.type";

import { SET_MERCHANT } from "@/store/mutations.type";

const initialState = { merchant: null, };
export const state = { ...initialState };


export const actions = {
  async [FETCH_MERCHANT](context, merchantFriendlyId) {
    const { data } = await MerchantsService.get(merchantFriendlyId);
    context.commit(SET_MERCHANT, data.merchant);
    return data;
  },
};


export const mutations = {
  [SET_MERCHANT](state, merchant) {
    state.merchant = merchant;
  },
};


const getters = {
  merchant(state) {
    return state.merchant;
  },
};


export default {
  state,
  actions,
  mutations,
  getters
};



