<template>
	<div class="container">
		<div v-if="isPageLoading">
			<div class="col-xs-12">
				Loading {{ merchant.name }}'s data
			</div>
		</div>
		<div v-if="!isPageLoading" class="row">
			<div class="col-xs-12">
				<img class="img-fluid" :src="merchant.image" />
				<h2><b>{{ merchant.name }}</b></h2>
				<p>{{ merchant.address }}</p>
				<hr>
				<h4 class="text-xs-center font-weight-bold">MENU</h4>
				<hr class="mb-0">
				<div classs="categoryList" v-for="(category, idx) in merchant.menu" :key="idx">
					<MenuCategory class="menuCategory" :menuCategory='category'></MenuCategory>
					<hr class="margin0">
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import { FETCH_MERCHANT } from "@/store/actions.type";
import { mapGetters } from "vuex";
import store from "@/store";

import MenuCategory from "@/components/MenuCategory";

export default {
  name: "viewMerchant",
  components: {
    MenuCategory
  },
  data() {
	  return {
			isPageLoading: false,
	  }
  },
  computed: {
    ...mapGetters(["merchant"])
	},
	// props: {
	// 	merchant: {
	// 		type: Object,
	// 		required: true
	// 	}
	// },
  beforeRouteEnter(to, from, next) {
    Promise.all([
			store.dispatch(FETCH_MERCHANT, to.params.merchantId),
    ]).then(() => {
      next();
    });
  },
}
</script>

<style scoped>
h2{
	margin-top: 10px;
}
p{
	margin-top: -10px;
}
.margin0{
	margin:0
}
.mb-0{
	margin-bottom: 0;
}
.menuCategory{
	margin-top: 5px;
}
</style>


